<script setup lang="ts">
    import { PropType, h, onMounted, onUnmounted, onUpdated, ref, useSlots } from "vue";
    import PerfectScrollbar from "perfect-scrollbar";
    import "perfect-scrollbar/css/perfect-scrollbar.css";
    const slots = useSlots();
    const props = defineProps({
        tag: { type: String, default: "div" },
        options: { type: Object as PropType<PerfectScrollbar.Options | undefined>, default: undefined },
    });

    const cvScrollRef = ref<HTMLElement | null>(null);
    const ps = ref<PerfectScrollbar | null>(null);

    onMounted(() => {
        if (!ps.value && !!cvScrollRef.value) {
            ps.value = new PerfectScrollbar(cvScrollRef.value, props.options);
            cvScrollRef.value.addEventListener("scroll", onScroll);
            cvScrollRef.value.addEventListener("ps-x-reach-start", onReachXStart);
            cvScrollRef.value.addEventListener("ps-x-reach-end", onReachXEnd);
        }
    });

    onUnmounted(() => {
        if (ps.value) {
            ps.value.destroy();
            ps.value = null;
        }
    });
    onUpdated(() => {
        update();
    });

    function render() {
        return h(
            props.tag,
            {
                class: "cv-scroll",
                ref: cvScrollRef,
            },
            slots.default && slots.default(),
        );
    }

    const emit = defineEmits(["scroll", "reach-x-start", "reach-x-end"]);
    function onScroll(ev: Event) {
        emit("scroll", ev);
    }
    function onReachXStart(ev: Event) {
        emit("reach-x-start", ev);
    }
    function onReachXEnd(ev: Event) {
        emit("reach-x-end", ev);
    }
    function update() {
        if (ps.value) {
            ps.value.update();
        }
    }

    defineExpose({ scrollElem: cvScrollRef, update });
</script>

<template>
    <render />
</template>

<style lang="sass" scoped>
    .cv-scroll
        position: relative
</style>
