<script setup lang="ts">
    import { PropType, computed, ref, useSlots } from "vue";
    import { ListItem, ListSize, Theme } from "../../types";
    import { ns } from "../../utils/ns";
    import { GpnCheckbox } from "..";
    import { useRipple } from "../../using";

    const props = defineProps({
        value: { required: true, type: Object as PropType<ListItem> },
        theme: { type: String as PropType<Theme>, default: "kp" },
        size: { type: String as PropType<ListSize>, default: "m" },
        multiple: { type: Boolean, default: false },
        selected: { type: Boolean },
    });

    const listNode = ref();
    if (!props.value.is_group) useRipple(listNode);
    const slots = useSlots();

    const classes = computed(() => {
        const _ns = new ns("list-node");
        return [
            _ns.namespace(),
            // _ns.block(props.view),
            _ns.block(props.size, "size"),
            props.value.is_group && _ns.block("group"),
            // _ns.block(props.form),
            // props.disabled && _ns.modifier('disabled'),
            // _ns.block(props.width, 'w'),
            // (props.iconLeft || props.iconRight) && _ns.block('with-icon'),
            // props.onlyIcon && _ns.block('only-icon'),
            // props.loading && _ns.modifier('loading'),
            `cv-theme-${props.theme}`,
            !!slots.default && _ns.modifier("is-slot"),
        ];
    });

    const emit = defineEmits(["click"]);
    function nodeClick(ev: Event) {
        if (props.value.is_group) return;
        emit("click", ev);
    }

    defineSlots<{
        default(props: { item: ListItem }): any;
    }>();
</script>

<template>
    <div ref="listNode" :class="classes" @click="nodeClick">
        <div v-if="props.multiple && !props.value.is_group">
            <GpnCheckbox :model-value="props.selected" :value="props.value" :theme="props.theme" />
        </div>
        <slot name="default" :item="props.value">
            <div v-tooltip.arrow.trunc="props.value.name" class="cv-list-node--name">{{ props.value.name }}</div>
        </slot>
    </div>
</template>

<style lang="scss">
    @import "./styles/";
</style>
