import { Ref, onMounted, onUnmounted } from "vue";

export function useClickOutside(target: Ref<HTMLElement | undefined>, handler: (event: PointerEvent | MouseEvent) => void) {
    const ignore: (Ref<HTMLElement> | string)[] = [];
    let shouldListen = true;

    const shouldIgnore = (event: PointerEvent | MouseEvent) => {
        return ignore.some((target) => {
            if (typeof target === "string") {
                return Array.from(window.document.querySelectorAll(target)).some((el) => el === event.target || event.composedPath().includes(el));
            } else {
                const el = target.value;
                return el && (event.target === el || event.composedPath().includes(el));
            }
        });
    };

    const listener = (event: PointerEvent | MouseEvent) => {
        const el = target.value;

        if (!el || el === event.target || event.composedPath().includes(el)) return;

        if (event.detail === 0) shouldListen = !shouldIgnore(event);

        if (!shouldListen) {
            shouldListen = true;
            return;
        }
        handler(event);
    };

    const listenerPointer = (event: PointerEvent) => {
        const el = target.value;
        shouldListen = !shouldIgnore(event) && !!(el && !event.composedPath().includes(el));
    };

    onMounted(() => {
        window.addEventListener("click", listener, { passive: true, capture: true });
        window.addEventListener("pointerdown", listenerPointer, { passive: true });
    });

    onUnmounted(() => {
        window.removeEventListener("click", listener, { capture: true });
        window.removeEventListener("pointerdown", listenerPointer);
    });
}
