import { getCurrentInstance as _getCurrentInstance } from "vue";
import { Router } from "vue-router";

function getCurrentInstance(name: string, message?: string) {
    const vm = _getCurrentInstance();

    if (!vm) {
        throw new Error(`${name} ${message || "must be called from inside a setup function"}`);
    }

    return vm;
}

export function useRouter(): Router | undefined {
    // return getCurrentInstance("useRouter")?.proxy?.$router;
    return getCurrentInstance("useRouter").appContext.config.globalProperties.$router;
}
