<script setup lang="ts">
    import { PropType, computed } from "vue";
    import { ns } from "../../utils/ns";
    import { ListSize, Theme, ListItem } from "../../types";
    import GpnListNode from "./GpnListNode.vue";
    import { GpnScrollbar } from "..";
    import { deepEqual } from "fast-equals";
    import PerfectScrollbar from "perfect-scrollbar";

    const props = defineProps({
        theme: { type: String as PropType<Theme>, default: "kp" },
        size: { type: String as PropType<ListSize>, default: "m" },
        items: { type: Array as PropType<ListItem[]>, default: () => [] },
        isClickable: { type: Boolean, default: true },
        withGroups: { type: Boolean, default: false },
        multiple: { type: Boolean, default: false },
        // modelValue: { type: Array as PropType<ListItem[]>, default: () => [] },
        modelValue: { type: [Array, Object] as PropType<ListItem[] | ListItem | undefined | null>, default: () => [] },
        multiline: { type: Boolean, default: false },
        scrollOptions: { type: Object as PropType<PerfectScrollbar.Options | undefined>, default: undefined },
    });

    const items = computed(() => (props.withGroups ? props.items : props.items.filter((f) => !f.is_group)));

    const classes = computed(() => {
        const _ns = new ns("list");
        return [
            _ns.namespace(),
            // _ns.block(props.view),
            _ns.block(props.size, "size"),
            props.isClickable && _ns.modifier("is-clickable"),
            props.multiline && _ns.modifier("multiline"),
            // _ns.block(props.form),
            // props.disabled && _ns.modifier('disabled'),
            // _ns.block(props.width, 'w'),
            // (props.iconLeft || props.iconRight) && _ns.block('with-icon'),
            // props.onlyIcon && _ns.block('only-icon'),
            // props.loading && _ns.modifier('loading'),
            `cv-theme-${props.theme}`,
        ];
    });

    const sortedItems = computed(() => {
        const _tree: ListItem[] = [];
        items.value
            .filter((f) => f.is_group)
            .forEach((f) => {
                _tree.push(f);
                _tree.push(...items.value.filter((i) => i.group_id === f.id));
            });
        if (props.withGroups) _tree.push(...items.value.filter((f) => !f.is_group && !f.group_id));
        else _tree.push(...items.value);
        return _tree;
    });

    const emit = defineEmits(["click", "update:modelValue", "select", "deselect"]);
    function onClick(item: ListItem) {
        if (Array.isArray(props.modelValue)) {
            const newModelValue = [...props.modelValue];
            const fItem = props.modelValue.find((f) => deepEqual(f, item));
            if (fItem) {
                newModelValue.splice(props.modelValue.indexOf(fItem), 1);
            } else {
                newModelValue.push(item);
            }
            emit("update:modelValue", newModelValue);
        } else {
            emit("update:modelValue", item);
        }
        emit("click", item);
    }

    function isSelected(item: ListItem) {
        if (Array.isArray(props.modelValue)) {
            return !!props.modelValue.find((f) => deepEqual(f, item));
        } else {
            return deepEqual(props.modelValue, item);
        }
    }
    defineSlots<{
        default(props: { item: ListItem; isSelected: boolean }): any;
    }>();
</script>

<template>
    <div :class="classes">
        <GpnScrollbar :options="props.scrollOptions">
            <GpnListNode
                v-for="item in sortedItems"
                :key="item.id"
                :class="{ 'cv-list-node--is-active': isSelected(item) }"
                :selected="isSelected(item)"
                :value="item"
                v-bind="props"
                @click="onClick(item)"
            >
                <template #default="scope">
                    <slot name="default" :item="scope.item" :is-selected="isSelected(item)"></slot>
                </template>
            </GpnListNode>
        </GpnScrollbar>
    </div>
</template>

<style lang="scss">
    @import "./styles/";
</style>
