<script lang="ts" setup>
    import { computed, type PropType, defineAsyncComponent, ref } from "vue";
    import type { IconSize, IconView, Theme } from "../../../types";
    import { ns } from "../../../utils/ns";
    // import "bootstrap-icons/font/bootstrap-icons.min.css";

    const props = defineProps({
        name: { type: String, required: true },
        view: { type: String as PropType<IconView>, default: "" },
        size: { type: String as PropType<IconSize | string>, default: "m" },
        theme: { type: String as PropType<Theme>, default: "kp" },
        color: { type: String, default: "" },
        svg: { type: Boolean, default: false },
    });

    const iconName = computed(() => {
        if (props.name.startsWith("bi-")) {
            return props.name;
        }
        const name = `../CustomIcons/${props.name}/${props.name}_size_${props.size}.vue`;
        return defineAsyncComponent(() => import(name));
    });

    const classes = computed(() => {
        const _ns = new ns("icon");
        return [_ns.namespace(), _ns.block(props.view || "default"), !isNaN(parseInt(props.size)) ? _ns.block("custom", "size") : _ns.block(props.size, "size"), `cv-theme-${props.theme}`];
    });

    const iconStyle = computed(() => {
        return [props.color && { color: props.color }, !isNaN(parseInt(props.size)) && { fontSize: props.size + "px" }];
    });

    const isBi = ref(props.name.startsWith("bi-"));
</script>

<template>
    <span :class="classes">
        <template v-if="!svg">
            <component :is="iconName" v-if="!isBi" />
            <template v-else>
                <i class="bi" :style="iconStyle" :class="iconName" />
            </template>
        </template>
        <template v-else>
            <slot name="default"></slot>
        </template>
    </span>
</template>

<style lang="sass">
    @import './styles/index'
</style>
