<script setup lang="ts">
    import { computed, onMounted, ref } from "vue";
    import type { PropType } from "vue";
    import { ns } from "../../utils/ns";
    import type { ButtonType, ButtonSize, ButtonWidth, ButtonForm, IconSize, Theme } from "../../types";
    import { GpnIcon, GpnLoader } from "..";
    import { useRipple } from "../../using";
    import { RouteLocationRaw } from "vue-router";
    import { useRouter } from "../../using/useRouter";

    const props = defineProps({
        theme: { type: String as PropType<Theme>, default: "kp" },
        label: { type: String, required: false, default: "" },
        view: { type: String as PropType<ButtonType>, default: "primary" },
        disabled: { type: Boolean, default: false },
        size: { type: String as PropType<ButtonSize>, default: "m" },
        width: { type: String as PropType<ButtonWidth>, default: "default" },
        form: { type: String as PropType<ButtonForm>, default: "default" },
        title: { type: String, default: "" },
        loading: { type: Boolean, default: false },
        iconLeft: { type: String, default: "" },
        iconRight: { type: String, default: "" },
        iconSize: { type: String as PropType<IconSize>, default: "" },
        onlyIcon: { type: Boolean, default: false },
        tag: { type: String, default: "button" },
        ripple: { type: Boolean, default: true },
        clear: { type: Boolean, default: false },
        to: { type: [String, Object] as PropType<RouteLocationRaw>, default: "" },
    });

    const buttonElem = ref();
    const router = useRouter();

    if (props.ripple) {
        useRipple(buttonElem);
    }
    onMounted(() => {});

    const classes = computed(() => {
        const _ns = new ns("btn");
        return [
            _ns.namespace(),
            _ns.block(props.view),
            _ns.block(props.size, "size"),
            _ns.block(props.form),
            props.disabled && _ns.modifier("disabled"),
            _ns.block(props.width, "w"),
            (props.iconLeft || props.iconRight || props.onlyIcon) && _ns.block("with-icon"),
            props.onlyIcon && _ns.block("only-icon"),
            props.loading && _ns.modifier("loading"),
            props.clear && _ns.modifier("clear"),
            `cv-theme-${props.theme}`,
        ];
    });

    const _iconSize = computed(() => {
        return props.iconSize || (props.size === "s" || props.size === "xs" ? "xs" : props.size === "m" ? "s" : "m");
    });

    const emit = defineEmits(["click"]);
    function onBtnClick(ev: Event) {
        if (props.loading) return;
        emit("click", ev);
        if (props.to) {
            router?.push(props.to);
        }
    }
</script>

<template>
    <component :is="tag" ref="buttonElem" :class="classes" @click="onBtnClick">
        <slot name="icon-left">
            <GpnIcon v-if="iconLeft" :name="iconLeft" :size="_iconSize" :theme="$props.theme" class="cv-btn-icon" :class="{ 'cv-btn-icon-left': !onlyIcon }" />
        </slot>
        <template v-if="!onlyIcon">{{ label }}</template>
        <slot name="icon-right">
            <GpnIcon v-if="iconRight && !onlyIcon" :name="iconRight" :theme="$props.theme" :size="_iconSize" class="cv-btn-icon cv-btn-icon-right" />
        </slot>
        <GpnLoader v-if="loading" size="s" :theme="$props.theme" />
    </component>
</template>

<style lang="scss">
    @import "./styles/";
</style>
