<script lang="ts" setup>
    import { computed, type PropType } from "vue";
    import type { LoaderSize, LoaderView, Theme } from "../../types/enums";
    import { ns } from "../../utils/ns";

    const props = defineProps({
        theme: { type: String as PropType<Theme>, default: "kp" },
        size: { type: String as PropType<LoaderSize>, default: "m" },
        view: { type: String as PropType<LoaderView>, default: "full" },
    });

    const classes = computed(() => {
        const _ns = new ns("loader");
        return [_ns.namespace(), _ns.block(props.size, "size"), _ns.block(props.view), `cv-theme-${props.theme}`];
    });
</script>

<template>
    <div :class="classes">
        <div class="cv-loader-dot"></div>
    </div>
</template>

<style lang="scss">
    @import "./styles/index";
</style>
