import { Ref, onMounted, onUnmounted } from "vue";
// import "../styles/_ripple.scss";

export function useRipple(elem: Ref<HTMLElement | undefined>) {
    const circleContainer = document.createElement("div");
    const circle = document.createElement("div");

    onMounted(() => {
        if (elem.value) elem.value.addEventListener("mousedown", createRipple);
    });
    onUnmounted(() => {
        if (elem.value) elem.value.addEventListener("mousedown", createRipple);
    });

    function createRipple(e: MouseEvent) {
        // console.log(e, elem.value);
        if (!elem.value) return;
        const rippleContElems = elem.value.getElementsByClassName("cv-ripple-container");
        for (let i = 0; i < rippleContElems.length; i++) {
            rippleContElems[i].replaceChildren();
            elem.value.removeChild(rippleContElems[i]);
        }

        circleContainer.appendChild(circle);
        elem.value.appendChild(circleContainer);
        const d = Math.max(elem.value.clientWidth, elem.value.clientHeight);
        const boundRect = elem.value.getBoundingClientRect();

        Object.assign(circle.style, {
            width: `${d}px`,
            height: `${d}px`,
            left: `${e.pageX - boundRect.left - d / 2}px`,
            top: `${e.pageY - boundRect.top - d / 2}px`,
        });
        circle.classList.add("cv-ripple");
        circleContainer.classList.add("cv-ripple-container");
    }
}
