export class ns {
    private _namespace = "";
    private defaultNamespace = "cv";

    constructor(elem: string) {
        this._namespace = `${this.defaultNamespace}-${elem}`;
    }

    public block(block: string, prefix = "") {
        return `${this._namespace}-${prefix}${prefix ? "-" : ""}${block}`;
    }
    public modifier(mod: string) {
        return `${this._namespace}__${mod}`;
    }

    public namespace() {
        return this._namespace;
    }
}

