<script setup lang="ts">
    import { computed } from "vue";
    import type { PropType } from "vue";
    import { ns } from "../../utils/ns";
    import type { CheckboxType, CheckboxSize, CheckboxAlign, Theme, CheckboxValue } from "../../types";
    import { GpnIcon } from "..";
    import { deepEqual } from "fast-equals";

    const props = defineProps({
        theme: { type: String as PropType<Theme>, default: "kp" },
        label: { type: String, required: false, default: "" },
        view: { type: String as PropType<CheckboxType>, default: "primary" },
        disabled: { type: Boolean, default: false },
        size: { type: String as PropType<CheckboxSize>, default: "m" },
        align: { type: String as PropType<CheckboxAlign>, default: "center" },
        // intermediate: { type: Boolean, default: false },
        nowrap: { required: false, type: Boolean, default: false },

        modelValue: { type: [Boolean, String, Number, Array, Object, Date, Symbol, undefined] as PropType<CheckboxValue>, default: undefined },
        checkedValue: { type: [Boolean, String, Number, Array, Object, Date, Symbol, undefined] as PropType<CheckboxValue>, default: true },
        uncheckedValue: { type: [Boolean, String, Number, Array, Object, Date, Symbol, undefined] as PropType<CheckboxValue>, default: false },
        value: { type: [Boolean, String, Number, Array, Object, Date, Symbol, undefined] as PropType<CheckboxValue>, default: undefined },
    });

    const classes = computed(() => {
        const _ns = new ns("checkbox");
        return [
            _ns.namespace(),
            _ns.block(props.view),
            _ns.block(props.size, "size"),
            _ns.block(props.align),
            props.disabled && _ns.modifier("disabled"),
            // props.intermediate && _ns.modifier("intermediate"),
            props.nowrap && _ns.modifier("nowrap"),
            checked.value && _ns.modifier("checked"),
            `cv-theme-${props.theme}`,
        ];
    });

    const checked = computed(() => {
        if (Array.isArray(props.modelValue)) {
            let found = false;
            for (const i of props.modelValue) {
                found = deepEqual(i, props.value);
                if (found) break;
            }
            return found;
        } else {
            return props.modelValue === props.checkedValue;
        }
    });

    const emit = defineEmits(["click", "update:modelValue", "input"]);

    function onClick(event: Event) {
        if (!props.disabled) {
            let newModelValue;
            let inputValue;

            if (typeof props.modelValue === "boolean") {
                newModelValue = checked.value ? props.uncheckedValue : props.checkedValue;
                inputValue = newModelValue;
            } else if (typeof props.modelValue === "object" && !Array.isArray(props.modelValue) && props.modelValue !== null) {
                //TODO: need object model here
                throw "Not implemented yet";
            } else if (Array.isArray(props.modelValue)) {
                newModelValue = [...props.modelValue];
                if (checked.value) {
                    newModelValue.splice(newModelValue.indexOf(props.value), 1);
                    inputValue = false;
                } else {
                    newModelValue.push(props.value);
                    inputValue = true;
                }
            } else {
                inputValue = checked.value ? props.uncheckedValue : props.checkedValue;
            }
            emit("click", event);
            emit("update:modelValue", newModelValue);
            emit("input", inputValue);
        }
    }
</script>

<template>
    <div :class="classes" @click="onClick">
        <input type="checkbox" class="cv-checkbox--input-hidden" :value="props.modelValue" :checked="checked" :disabled="disabled" />
        <div class="cv-checkbox--input">
            <GpnIcon v-if="checked" name="bi-check2" :size="props.size === 's' ? 'xs' : props.size" />
        </div>
        <span v-tooltip.arrow.trunc="label" class="cv-checkbox--label">{{ label }}</span>
    </div>
</template>

<style lang="scss">
    @import "./styles/";
</style>
